			day: '0',
			hour: '00',
			min: '00',
			second: '00',
			id:'',
			tagsItem:[],
			items:{},
			background: ['color1', 'color2', 'color3'],
			indicatorDots: false,
			autoplay: true,
			interval: 2000,
			duration: 500,
			amapPlugin: null,
			key: 'a93eba2f3c61d2468a451b83bf662377',
